import React, { useState } from 'react';
import { makeStyles } from '@mui/styles';
import { 
    Button,
    Dialog,
    Paper,
    Typography,
    Box,
    LinearProgress
} from '@mui/material';
import soyImage from '../assets/images/illu_soy2-20 1.png';
import Icon3 from '../assets/images/icon3.svg';

const useStyles = makeStyles(theme => ({
    button: {
        boxShadow: '0px 2px 10px rgba(205, 19, 34, 0.08)!important',
        fontFamily: 'Poppins-SemiBold!important',
        fontSize: '12px!important',
        letterSpacing: '0.01em!important',
        textTransform: 'capitalize!important',
        minHeight: '37px',
    },
    foodImage: {
        display: 'block',
        margin: 'auto'
    },
    borderLinearProgress: {
        height: '6px!important',
        borderRadius: 3,
        margin: '6px 0',
        backgroundColor: '#EBEFF2!important',
        "& > span": {
            borderRadius: 3
        }
    },
    input: {
        color: '#105157',
        fontFamily: 'Poppins-SemiBold',
        fontSize: '12px',
        letterSpacing: '0.01em',
        textTransform: 'capitalize',
        border: '1px solid #105157',
        borderRadius: '4px',
        outline: 'none',
        textAlign: 'center',
        marginRight: '12px',
        width: '30%',
    }
}));

const FeedModal = ({open, onClose}) => {
    const classes = useStyles();
    const [number, setNumber] = useState(0);
    return (
        <Dialog onClose={onClose} open={open} sx={{
            "& .MuiDialog-container > .MuiPaper-root": {
                maxWidth: 460,
                width: '100%'
            }
        }}>
            <Paper sx={{
                padding: '46px 24px 24px'
            }}>
                <Typography
                    sx={{
                        fontFamily: 'Poppins-Bold',
                        fontSize: '15px',
                        lineHeight: '22px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#192A3E',
                    }}
                >
                    Feed your Rooster
                </Typography>
                <Typography
                    sx={{
                        fontSize: '12px',
                        fontFamily: 'Poppins-Regular',
                        lineHeight: '18px',
                        textAlign: 'center',
                        letterSpacing: '0.01em',
                        color: '#4C5862',
                    }}
                >
                    Use your SOY to improve your egg rarity<br />
                    and get more chance for better openings.
                </Typography>
                <img className={classes.foodImage} src={soyImage} alt="" />
                <Box sx={{marginBottom: '24px'}}>
                    <Box sx={{
                        display: 'flex',
                        alignItems: 'center'
                    }}>
                        <img src={Icon3} alt="" />
                        <Typography
                            sx={{
                                fontSize: '12px',
                                fontFamily: 'Poppins-Regular',
                                lineHeight: '18px',
                                letterSpacing: '0.01em',
                                color: '#4C5862',
                                margin: '0 12px 0 8px',
                                flexGrow: '1'
                            }}
                        >
                            Hunger
                        </Typography>
                        <Typography
                            sx={{
                                fontSize: '10px',
                                fontFamily: 'Poppins-Regular',
                                lineHeight: '15px',
                                textAlign: 'right',
                                letterSpacing: '0.01em',
                                color: '#4C5862',
                                opacity: '0.5'
                            }}
                        >
                            (1/10)
                        </Typography>
                    </Box>
                    <LinearProgress 
                        className={classes.borderLinearProgress} 
                        variant="determinate" 
                        value={10} 
                        sx={{
                            "& > span": {
                                backgroundColor: '#105157'
                            }
                        }}
                    />
                </Box>
                <Box sx={{
                    display: 'flex',
                    marginBottom: '12px'
                }}>
                    <input
                        value={number}
                        onChange={(e) => setNumber(e.target.value)}
                        className={classes.input}
                    />
                    <Button 
                        variant='contained'
                        className={classes.button}
                        onClick={onClose}
                        sx={{
                            flexGrow: '1',
                            backgroundColor: '#105157',
                            "&:hover": {
                                backgroundColor: '#105157',
                            }
                        }}
                    >
                        Feed with Soy
                        <Typography
                            component="span"
                            sx={{
                                marginLeft: '4px',
                                fontSize: '10px',
                                fontFamily: 'Poppins-Regular',
                            }}
                        >
                            (Max. 900 SOY)
                        </Typography>
                    </Button>
                </Box>
                <Button 
                        variant='contained'
                        className={classes.button}
                        onClick={onClose}
                        fullWidth
                        sx={{
                            backgroundColor: '#CD1322',
                            marginBottom: '12px',
                            "&:hover": {
                                backgroundColor: '#CD1322',
                            }
                        }}
                    >
                        Or Stack to Feed
                    </Button>
                <Button
                    variant='outlined'
                    fullWidth
                    onClick={onClose}
                    sx={{
                        color: '#CD1322',
                        fontFamily: 'Poppins-SemiBold',
                        fontSize: '12px',
                        letterSpacing: '0.01em',
                        textTransform: 'capitalize',
                        borderColor: '#CD1322',
                        "&:hover": {
                            borderColor: '#CD1322',
                        }
                    }}
                >
                    Cancel
                </Button>
            </Paper>
        </Dialog>
    );
}

export default FeedModal;